export default {
	data() {
		return {
			list:[],
			active:false,
			value:'',
			activeIndex:0,
			text:'全部',
			textShow:false
		}
	},
	onLoad() {

	},
	computed:{
		listData(){
			let list=JSON.parse(JSON.stringify(this.list));
			let newList=[];
			//all
			if(this.activeIndex===0){
				this.text='全部';
				return list;
			}
			//loading
			if(this.activeIndex===1){
				this.text='待办';
				list.forEach((item)=>{
					if(!item.checked){
						newList.push(item);
					}
				})
				return newList;
			}
			//complete
			if(this.activeIndex===2){
				this.text='已完成';
				list.forEach((item)=>{
					if(item.checked){
						newList.push(item);
					}
				})
				return newList;
			}
		}
	},
	methods: {
		create(){
			if(!this.active){
				this.open();
			}else{
				this.close();
			}
		},
		open(){
			this.active=true;
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.textShow=true;
				},50);
			})
		},
		close(){
			this.textShow=false;
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.active=false;
				},300);
			})
		},
		add(){
			if(this.value===''){
				uni.showToast({
					title:"请输入内容"
				})
				return
			}
			this.list.unshift({content:this.value,id:'id'+new Date().getTime(),checked:false});
			this.value='';
			this.close();
		},
		//点击完成待办事项
		finish(id){
			let index=this.list.findIndex((item)=>item.id===id);//获取下标
			this.list[index].checked=!this.list[index].checked;
		},
		tab(index){
			this.activeIndex=index;
			
		}
	}
}
